<template>
  <v-container >
    <div class="header">
      <div @click="goBack" class="goBack">
        <v-icon size="28">chevron_left</v-icon>
      </div>
      <p class="menu_title"></p>
    </div>
    <v-row class="mt-7">
      <v-col cols="6" offset="3" sm="4" offset-sm="4">
        <v-img src="@/assets/loginimage/paul_logo.png"></v-img>
      </v-col>
    </v-row>

    <v-row class="mt-7">
      <v-col cols="10" offset="1" sm="10" offset-sm="1">
        <form @submit.prevent="DoLogin" ref="form">
          <h5>()</h5>
          <v-text-field
            name="user_id"
            v-model="user_id"
            type="email"
            outlined
            required
            class="mt-1"
            :rules="user_id_rule"
          ></v-text-field>

          <h5></h5>
          <v-text-field
            name="Password"
            type="password"
            v-model="pwd"
            outlined
            required
            class="mt-1"
            :rules="pwd_rule"
          ></v-text-field>

          <v-btn
            type="submit"
            color="grey darken-4"
            dark
            v-if="!getLoading"
            size="x-large"
            width="100%"
            height="50px"
          ></v-btn>

          <p class="text-center mt-3">
              ?
            <span class="crownred cursor" @click="toJoin"></span>
          </p>

          <v-progress-circular indeterminate :width="7" :size="70" color="green" v-if="getLoading"></v-progress-circular>

          <v-alert class="mt-3" type="error" dismissible v-model="checkAlert">
            
            {{ getErrorMsg }}
          </v-alert>
        </form>
      </v-col>
    </v-row>
  
    <v-row class="mt-14">
      <v-col cols="3" offset="1" class="center">
        <v-divider class="box" />
      </v-col>
      <v-col cols="4">
        <p class="text-center">SNS </p>
      </v-col>
      <v-col cols="3" class="center">
        <v-divider class="box" />
      </v-col>
    </v-row>

    <v-row class="mt-2">
      <v-col cols="10" offset="1">
        <v-img contain src="@/assets/loginimage/googlelogin.png" class="cursor" @click="DoGoogleLogin_Popup"></v-img>
      </v-col>
      <v-col cols="10" offset="1" sm="5" offset-sm="0" class="hidden">
        <v-img contain src="@/assets/loginimage/kakaologin.png" class="cursor"></v-img>
      </v-col>
    </v-row>
  </v-container>
</template>


<script>
export default {
  mounted() {
    //    
    const users = [
      { id: 1, uid: "111111@naver.com", upw: "123456", uname: '1' },
      { id: 2, uid: "222222", upw: "1234", uname: '2' },
      { id: 3, uid: "333333", upw: "1234", uname: '3' },
      { id: 4, uid: "444444", upw: "1234", uname: '4' },
    ];
    localStorage.setItem('users', JSON.stringify(users));
  },
  data() {
    return {
      name: "Login",
      valid: false,
      user_id: "",
      user_id_rule: [
        v => !!v || " ",
        v =>
          /^[A-Za-z0-9_\.\-]+@[A-Za-z0-9\-]+\.[A-za-z0-9\-]+/.test(v) ||
          "   .",
          v => v.length > 3 || " 3  "
        ],
        pwd: "",
        pwd_rule: [
          v => !!v || " ",
          v => v.length <= 16 || " 6~16 ",
          v => v.length > 6 || " 6~16 "
        ],
        checkAlert: false,
      };
  },
  computed: {
    getLoading() {
      this.$store.getters.getisLoading;
    },
    getErrorMsg() {
      this.$store.getters.getErrorMsg;
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    DoGoogleLogin_Popup() { //     
        this.$store.dispatch("DoGoogleLogin_Popup")
      },
      toJoin() {
      this.$router.push("Join");
    },
    DoLogin() {
      const users = JSON.parse(localStorage.getItem('users')) || [];
      const user = users.find(u => u.uid === this.user_id && u.upw === this.pwd);

      if (user) {
        alert(`, ${user.uname}.`);
        localStorage.setItem('loggedInUser', JSON.stringify(user));
        this.$router.push('/');
      } else {
        alert('   .');
      }

      this.$store.dispatch("DoLogin", {
        //  (payload)
        sEmail: this.user_id,
        sPwd: this.pwd
      });
    }
  },
  watch: {
    // getErrorMsg       
    getErrorMsg(errMsg) {
      if (errMsg) {
        this.checkAlert = true;
      }
    },
    checkAlert(val) {
      // checkAlert  false  store   
      if (val === false) {
        this.$store.commit("setErrorMsg", "");
      }
    }
  }
};
</script>

<style scoped>
.center {
  padding-top: 24px !important ;
}

.crownred {
  color: #e80a4d;
}

.cursor {
  cursor: pointer;
}


.hidden{
  display: none;
}

</style>