<template>
  <div>
    <div class="header">
      <p class="menu_title"></p>
    </div>
    <v-tabs fixed-tabs v-model="tab" color="black">
      <v-tab href="#gift"></v-tab>
      <v-tab href="#received"></v-tab>
      <v-tab href="#orders"></v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <div class="content-height">
        <!--  -->
        <v-tab-item value="gift">
          <p class="center-text margin-bottom">
            Paul Bassett    e-Card<br /> / .
          </p>
          <div class="card-background">
            <!-- Centered buttons in a row -->
            <v-row justify="center">
              <v-col cols="auto">
                <v-btn
                  class="card-type no-shadow"
                  :class="{ 'selected-btn': selectedBtn === '' }"
                  @click="selectButton('')"
                >
                  
                </v-btn>
              </v-col>
              <v-col cols="auto">
                <v-btn
                  class="card-type no-shadow"
                  :class="{ 'selected-btn': selectedBtn === '' }"
                  @click="selectButton('')"
                >
                  
                </v-btn>
              </v-col>
              <v-col cols="auto">
                <v-btn
                  class="card-type no-shadow"
                  :class="{ 'selected-btn': selectedBtn === '' }"
                  @click="selectButton('')"
                >
                  
                </v-btn>
              </v-col>
              <v-col cols="auto">
                <v-btn
                  class="card-type no-shadow"
                  :class="{ 'selected-btn': selectedBtn === '' }"
                  @click="selectButton('')"
                >
                  
                </v-btn>
              </v-col>
            </v-row>
            <!-- Image Carousel with Pager -->
            <v-row justify="center">
              <v-col cols="auto">
                <v-carousel
                  v-model="carouselIndex"
                  hide-delimiters
                  :show-arrows="false"
                >
                  <v-carousel-item
                    v-for="(image, index) in images[selectedBtn]"
                    :key="index"
                  >
                    <v-img
                      :src="require(`@/assets/cards/${image}`)"
                      class="card-rounded no-shadow"
                      cover
                    >
                      <!-- Pager -->
                      <div class="pager">
                        {{ carouselIndex + 1 }} / {{ images[selectedBtn].length }}
                      </div>
                    </v-img>
                  </v-carousel-item>
                </v-carousel>
              </v-col>
            </v-row>
          </div>

          <v-container flat class="pt-5 pb-2 h-200 content-margin">
            <div class="left-align hori-align">
              <p class="font-weight-medium"> </p>
              <div class="btn-align">
                <v-btn class="receiver-btn no-shadow margin-right"> </v-btn>
                <v-btn class="receiver-btn no-shadow"> </v-btn>
              </div>
            </div>

            <div class="left-align">
              <v-text-field
                class="name margin-right"
                v-model="recipientName"
                placeholder="(30 )"
                outlined
                dense
              ></v-text-field>
              <v-text-field
                class="number"
                v-model="recipientNumber"
                placeholder=" "
                outlined
                dense
              ></v-text-field>
            </div>

            <p class="font-weight-medium left-align"> </p>

            <v-textarea
              class="message left-align text-padding"
              v-model="message"
              placeholder=" ."
            ></v-textarea>
          </v-container>

          <div class="gray-line"></div>

          <v-container flat class="pt-5 pb-2 content-margin">
            <div class="left-align hori-align">
              <p class="font-weight-medium">  </p>
              <p><span class="text-bold">{{ formattedAmount }}</span> </p>
            </div>
            <div class="left-align amount-align">
              <v-btn
                class="no-shadow amount-btn"
                :class="{ 'selected-btn': selectedAmountBtn === 10000 }"
                @click="selectAmount(10000)"
              >10,000</v-btn>
              <v-btn
                class="no-shadow amount-btn"
                :class="{ 'selected-btn': selectedAmountBtn === 30000 }"
                @click="selectAmount(30000)"
              >30,000</v-btn>
              <v-btn
                class="no-shadow amount-btn"
                :class="{ 'selected-btn': selectedAmountBtn === 50000 }"
                @click="selectAmount(50000)"
              >50,000</v-btn>
            </div>
            <div class="left-align amount-align">
              <v-btn
                class="no-shadow amount-btn"
                :class="{ 'selected-btn': selectedAmountBtn === 100000 }"
                @click="selectAmount(100000)"
              >100,000</v-btn>
              <v-btn
                class="no-shadow amount-btn"
                :class="{ 'selected-btn': selectedAmountBtn === 200000 }"
                @click="selectAmount(200000)"
              >200,000</v-btn>
              <v-btn
                class="no-shadow amount-btn"
                :class="{ 'selected-btn': selectedAmountBtn === 'custom' }"
                @click="openCustomAmountModal"
              ></v-btn>
            </div>

            <!-- Custom Amount Modal -->
            <v-dialog v-model="customAmountDialog" max-width="300px">
              <v-card>
                <v-card-title> </v-card-title>
                <v-card-text>
                  <v-text-field
                    v-model="customAmount"
                    label=" "
                    type="text"
                    :value="formattedCustomAmount"
                    @input="customAmount = $event.replace(/,/g, '')"
                  ></v-text-field>
                </v-card-text>
                <v-card-actions>
                  <v-btn color="black" text @click="cancelCustomAmount"></v-btn>
                  <v-btn color="black" text @click="saveCustomAmount"></v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-container>

          <div class="gray-line"></div>

          <v-container flat class="pt-5 pb-2 content-margin">
            <p class="left-align hori-align font-weight-medium"> </p>
            <div class="payment-align hori-align">
              <input 
              type="radio" 
              id="payment" 
              name="credit" 
              value-="credit"
              checked="checked"
              class="margin-right">
              <label for="payment"></label>
            </div>
          </v-container>

          <div class="d-flex justify-center">
            <v-btn 
            class="send-btn no-shadow" 
            color="black" 
            @click="showPaymentCompleteDialog">
             </v-btn>
          </div>

          <v-dialog v-model="paymentCompleteDialog" max-width="500px">
            <v-card>
              <v-card-title class="text-center">
                <span class="payment-complete-text"> </span>
              </v-card-title>
              <v-card-actions class="justify-center">
                <v-btn color="black" @click="paymentCompleteDialog = false" class="text-white"></v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <div class="left-align hori-align card-info">
            e-Card 
          </div>
          
          <v-container flat class="pb-2 content-margin">
            <div>
                      LMS  .       .
              <br>
              e-Card    ,    .
              <br>
              e-Card 1    10,000 ~ 500,000 ,     .
              <br>
              e-Card ,   100 . (,  the Red Edition      e-Card  )  30 ,  30    .
              <br>
                       . 
              <br>
               e-Card   ( 7)  [e-Card  ] > []  . ,           .
              <br>
              e-Card   3 . 
              <br>
              ()    e-Card   3   3  ,        .
            </div>
          </v-container>

        </v-tab-item>

        <!--  -->
        <v-tab-item value="received">
          <v-card flat>
            <v-card-text>
              <p>   .</p>
            </v-card-text>
          </v-card>
        </v-tab-item>

        <!--  -->
        <v-tab-item value="orders">
          <v-card flat>
            <v-card-text>
              <p>   .</p>
            </v-card-text>
          </v-card>
        </v-tab-item>
      </div>
    </v-tabs-items>
  </div>
</template>

<script>
export default {
  name: "gift",
  data() {
    return {
      tab: "gift",
      carouselIndex: 0,
      selectedBtn: "",
      images: {
        "": ["pb_1.png", "pb_2.png", "pb_3.png", "pb_4.png", "pb_5.png", "pb_6.png", "pb_7.png", "pb_8.png"],
        "": ["season_1.png", "season_2.png", "season_3.png", "season_4.png"],
        "": ["thanks_1.png", "thanks_2.png", "thanks_3.png"],
        "": ["congrats_1.png", "congrats_2.png", "congrats_3.png", "congrats_4.png"],
      },
      selectedAmount: 0,
      customAmountDialog: false,
      customAmount: "", // This will hold the raw number for input
      paymentCompleteDialog: false, // New data property for dialog
      // Add the new properties here
      recipientName: '',
      recipientNumber: '',
      message: '',
    };
  },
  computed: {
    formattedAmount() {
      return this.selectedAmount.toLocaleString();
    },
    formattedCustomAmount() {
      return this.customAmount ? parseFloat(this.customAmount).toLocaleString() : '';
    }
  },
  methods: {
    selectButton(buttonName) {
      this.selectedBtn = buttonName;
      this.carouselIndex = 0;
    },
    selectAmount(amount) {
      this.selectedAmount = amount;
      this.selectedAmountBtn = amount;
    },
    openCustomAmountModal() {
      this.customAmountDialog = true;
    },
    saveCustomAmount() {
      const parsedAmount = parseFloat(this.customAmount.replace(/,/g, ''));
      if (!isNaN(parsedAmount) && parsedAmount > 0) {
        this.selectedAmount = parsedAmount;
        this.selectedAmountBtn = 'custom';
        this.customAmountDialog = false;
      }
    },
    cancelCustomAmount() {
      this.customAmountDialog = false;
    },
    showPaymentCompleteDialog() {
      this.paymentCompleteDialog = true;
      this.resetForm();  // Call the resetForm method to reset everything
    },
    resetForm() {
      // Reset form fields
      this.recipientName = '';
      this.recipientNumber = '';
      this.message = '';

      // Reset selected card type
      this.selectedBtn = '';

      // Reset image carousel
      this.carouselIndex = 0;

      // Reset selected amount
      this.selectedAmount = 0;
      this.selectedAmountBtn = null;

      // Scroll to top
      window.scrollTo(0, 0);
    },
  }
};
</script>


<style scoped>
.content-height {
  height: 1600px;
}
.margin-bottom{
  margin-bottom: 15px;
}
.content-margin {
  padding-left: 20px !important;
  padding-right: 20px !important;
}
.center-text {
  text-align: center;
  margin-top: 15px;
}
.card-background {
  background-color: #f7f7f7;
  padding-top: 25px;
}
.h-200 {
  margin-top: 15px;
}
.card-type {
  border-radius: 30px;
  border: 1px solid #000;
  width: 75px;
  height: 40px;
}
.card-rounded {
  border-radius: 15px;
  border: 1px solid #fbfbfb;
  width: 320px;
  height: 190px;
}
.selected-btn {
  background-color: black !important;
  color: white !important;
}
.pager {
  position: absolute;
  bottom: 13px;
  right: 25px;
  background-color: rgba(146, 146, 146, 0.6);
  color: white;
  padding: 4px 8px;
  border-radius: 13px;
  font-size: 14px;
}
.no-shadow {
  box-shadow: none !important;
}
.v-carousel {
  height: 200px !important;
}
.receiver-btn {
  border: 1px solid #000;
  width: 125px;
  border-radius: 7px;
}
.name {
  border: 1px solid #000;
  height: 40px;
  width: 140px;
}
.number {
  border: 1px solid #000;
  height: 40px;
  width: 190px;
}
.message {
  border: 1px solid #000;
  border-radius: 5px;
  height: 155px;
}
.left-align {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}
.btn-align {
  display: flex;
}
.margin-right {
  margin-right: 10px;
}
.hori-align {
  vertical-align: middle;
  align-content: center;
}
.text-padding {
  padding: 12px 15px;
}
.gray-line {
  height: 12px;
  background-color: #f7f7f7;
}
.amount-btn {
  border: 1px solid #000;
  width: 120px;
}
.hide-btn {
  display: none;
}
.text-bold{
  font-weight: bold;
}
.payment-align{
  justify-content: left;
}
.send-btn{
  margin-top: 15px;
  display: block;
  width: 390px;
  height: 40px !important;
  color: #fff;
  margin-bottom: 20px;
}
.card-info{
  height: 35px;
  background-color: #f7f7f7;
  padding: 5px 20px;
}
.payment-complete-text {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}
.text-white{
  color: #fff;
}

</style>
