<template>
  <v-dialog v-model="isOpen" max-width="300" persistent>
    <div class="modal_card">
      <div class="modal_content">
        <div class="modal_message">
          <slot> </slot>
        </div>
      </div>

      <div class="modal_actions">
        <v-btn
          v-if="showConfirmButton"
          class="modal_button modal_confirm_button"
          color="black"
          @click="handleConfirm"
        >
          {{ confirmText }}
        </v-btn>
        <v-btn
          v-if="showCancelButton"
          class="modal_button modal_cancel_button"
          color="grey"
          @click="handleCancel"
        >
          {{ cancelText }}
        </v-btn>
      </div>
    </div>
  </v-dialog>
</template>

<script>
export default {
  props: {
    isOpen: {
      type: Boolean,
      required: true
    },
    confirmText: {
      type: String,
      default: ''
    },
    cancelText: {
      type: String,
      default: ''
    },
    showCancelButton: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    showConfirmButton() {
      return true; //     
    }
  },
  methods: {
    handleConfirm() {
      this.$emit('confirm');
      this.closeModal();
    },
    handleCancel() {
      this.$emit('cancel');
      this.closeModal();
    },
    closeModal() {
      this.$emit('update:isOpen', false);
    }
  }
};
</script>

<style scoped>
.modal_card {
  height: 150px;
  display: flex;
  flex-direction: column;
  background: #ffffff;
  justify-content: space-between;
}

.modal_content {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.modal_actions {
  display: flex;
}

.modal_button {
  flex: 1; /*       */
  height: 30px;
  font-size: 16px;
  border-radius: 0;
  padding: 0;
}

.modal_confirm_button {
  color: white;
  background-color: black;
}

.modal_cancel_button {
  color: white;
  background-color: grey;
}
</style>
