export default {
    state: {
        IsLoading: false, //  
        ErrorMessage: "" //  
    },
    mutations: {
        SetLoading(state, payload) {
            state.IsLoading = payload
        },
        SetErrorMessage(state, payload) {
            state.ErrorMessage = payload
        }
    },
    getters: {
        getLoading(state) {
            return state.getLoading
        },
        getErrorMsg(state) {
            return state.ErrorMessage
        }
    }
}